.container {
  width: 100%;
  height: 100vh;
  display: flex;
  background-color: #01172e;
  flex-direction: row-reverse;
  align-items: flex-end;
  overflow: hidden;
}

.visualizeContainer {
  margin: 0 5px 0 5px;
  width: 100%;
  min-width: 500px;
  height: 100%;
  min-height: 500px;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-evenly;
  background-color: inherit;
}

.arrayBar {
  width: 80px;
  margin: 0px 1px 2px 1px;
  transition: height 0.2s;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  padding-top: 10px;
  border: 0.5px solid black;
}

.arrayBar > span {
  background-color: inherit;
  margin-bottom: 5px;
  color: black;
  font-weight: 800;
}

.button {
  width: 12rem;
  height: 3rem;
  padding: 0.3rem 0rem;
  border: none;
  border-radius: 0.317rem;
  background-color: #1481ba;
  color: #fff;
  font-weight: 700;
  font-size: 1rem;
  cursor: pointer;
}

.button:hover {
  opacity: 0.85;
}

.sidebar {
  width: 25vw;
  min-width: 200px;
  height: 100%;
  min-height: 500px;
  padding: 0 1.5vw 0 1.5vw;
  background-color: #001021;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.slider-container {
  height: 3rem;
  width: 12rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: inherit;
}

.slider-container > input {
  width: 100%;
  cursor: pointer;
}

label {
  color: #fff;
  background-color: inherit;
  font-size: 1.1rem;
}

.select-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 200px;
  background-color: inherit;
  font-size: 16px;
  cursor: pointer;
}

header {
  background-color: inherit;
  color: #e7d9ea;
  font-size: 25px;
  text-align: center;
  line-height: 2rem;
  font-weight: 600;
  border: 2px solid white;
  border-radius: 20px;
  padding: 10px;
}

#select {
  height: 35px;
  padding: 5px 7px;
  line-height: 20px;
  font-size: 18px;
  width: 100%;
  border: 2px solid green;
  border-radius: 6px;
  margin-top: 10px;
}

.select-box select:focus {
  border: 2px solid green;
}

.slt {
  cursor: pointer;
}

.social {
  font-size: 20px;
  color: white;
}

a {
  text-decoration: none;
  background-color: #001021;
  color: white;
}

a:link {
  text-decoration: none;
  background-color: #001021;
}

a:visited {
  text-decoration: none;
  background-color: #001021;
}

a:hover {
  text-decoration: none;
  background-color: #001021;
}

a:active {
  text-decoration: none;
  background-color: #001021;
}
